// 标签搜索
.tea-search.tea-search--tags {
  min-height: 30px;
  height: 30px;
  overflow: hidden;
  .tea-tag{
    .tea-icon-dismiss{
      display: none;
    }
  }
  &.is-active{
    height:auto;
    .tea-search__inner{
      border-color:#006eff;
    }
    .tea-tag{
      padding:0 20px 0 5px;
      span{
        display: inline;
        max-width: initial;
        white-space: normal;
      }
      .tea-icon-dismiss{
        display: inline-block;
      }
    }
  }
  .tea-btn {
    width: 16px;
    height: 16px;
    min-width: 16px;
    bottom: 7px;
    padding: 0px;
    vertical-align: middle;
    &--search {
      right: 5px;
    }
    &--dismiss {
      right: 47px;
    }
    &--info {
      right: 26px;
    }
  }
  .tea-search {
    &__inner {
      padding: 0 10px 4px;
      border: 1px solid #ddd;
      font-size: 0;
      min-height: 30px;
      box-sizing: border-box;
    }
  }
  & .tea-input--tag {
    height: 20px;
    line-height: 20px;
    padding: 0 5px 0 0px;
  }
}

.tea-input--tag {
  width: auto;
  min-width: 1px;
  position: initial;
  border: none;
  height: 28px;
  vertical-align: top;
  margin-top: 4px;
}

