
// Text alignment

.tea-text-justify {
  text-align: justify !important;
}
.tea-text-wrap {
  white-space: normal !important;
}
.tea-text-nowrap {
  white-space: nowrap !important;
}

.tea-text-left {
  text-align: left!important;
}
.tea-text-center {
  text-align: center!important;
}
.tea-text-right {
  text-align: right!important;
}

// Text wrapping


// Text overflow
.tea-text-overflow {
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}
.tea-text-truncate {
  display: inline-block;
  vertical-align: middle;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
}

