.tea-step {
  counter-reset: step;
  font-size: 0;
  margin-top: -10px;

  &__item {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    margin-top: 10px;
  }

  &__num {
    width: 28px;
    height: 28px;
    background-image: url(assets/image/slice/svg/step-back-ok.svg);
    box-sizing: border-box;
    line-height: 22px;
    display: inline-block;
    border-radius: 50%;
    margin-right: 10px;
    text-align: center;
    color: #fff;
    vertical-align: middle;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 6px;
      top: 6px;
      background-image: url(assets/image/slice/svg/step-num.svg);
      background-color: transparent;
    }

    &::before {
      counter-increment: step;
      content: counter(step);
      font-size: 0;
    }

  }

  &__content {
    display: inline-block;
    vertical-align: middle;
    font-size: 14px;
  }
  
  &__title {
    font-weight: 700;
    max-width: 10em;
  }

  &__arrow {
    width: 16px;
    height: 16px;
    content: "";
    display: inline-block;
    margin: 0 30px;
    vertical-align: middle;
    background-image: url(assets/image/slice/svg/step-arrow.svg);
  }

  &__description {
    font-size: 12px;
    color: #888;
    line-height: 18px;
    margin-top: 10px;
  }

}

// 流程说明
  .tea-step.tea-step--alternative {
    .tea-step {

      &__item {
        text-align: center;
        padding-right: 160px;

        &:last-of-type {
          padding-right: 0
        }

      }

      &__num {
        margin-right: 0;
        background-image: url(assets/image/slice/svg/step-back-alternative.svg);

        &::after {
          display: none;
        }

        &::before {
          font-size: 16px;
          color: #006EFF;
          font-weight: 700;
          line-height: 28px;
        }

      }

      &__content {
        display: block;
        margin-top: 10px;
      }

      &__arrow {
        width: 120px;
        height: 1px;
        content: "";
        display: inline-block;
        margin: 0 0 0 -140px;
        line-height: 1.2;
        position: absolute;
        left: 100%;
        top: 50%;
        background-image: url(assets/image/slice/svg/step-line.svg);
      }

      &__tips {
        position: absolute;
        left: 100%;
        top: 0;
        width: 120px;
        margin: 0 0 0 -140px;
        overflow: hidden;
        line-height: normal;
        font-size: 12px;
        color: #888;
        word-break: break-word;
      }

      &__title {
        margin-top: 4px;
      }

    }
  }

   // 垂直步骤条
  .tea-step.tea-step--vertical {
    .tea-step {

      &__item {
        display: block;
        padding-bottom: 20px;
      }

      &__num {
        vertical-align: top;
        background-image: url(assets/image/slice/svg/step-back-alternative.svg);

        &::after {
          display: none;
        }

        &::before {
          font-size: 16px;
          color: #006EFF;
          font-weight: 700;
          line-height: 28px;
        }

      }

      &__content {
        vertical-align: top;
      }

      &__title {
        margin-top: 4px;
      }

      &__arrow {
        height: 100%;
        width: 1px;
        vertical-align: middle;
        margin-left: 20px;
        margin-right: 30px;
        background-color: #ddd;
        position: absolute;
        top: 28px;
        bottom: 0;
        left: -6px;
      }

    }
    
  }

  // 垂直步骤条·圆点

  .tea-step.tea-step--dot {

    .tea-step {

      &__item {
        display: block;
        padding-bottom: 20px;
      }

      &__num {
        vertical-align: top;
        width: 9px;
        height: 9px;
        margin-top: 8px;
        background-color: #006eff;

        &::after {
          display: none;
        }
      }

      &__content {
        vertical-align: top;
      }

      &__title {
        margin-top: 2px;
      }

      &__arrow {
        height: 100%;
        width: 1px;
        vertical-align: middle;
        margin-left: 20px;
        margin-right: 30px;
        background-color: #ddd;
        position: absolute;
        top: 17px;
        bottom: 0;
        left: -16px;
      }

    }

  }

// 已完成和未完成的步骤状态处理
.tea-step__item.is-current ~ .tea-step__item  {
  color: #bbb;
}

.tea-step__item.is-current .tea-step__num::before,
.tea-step__item.is-current ~ .tea-step__item .tea-step__num::before {
    font-size: 16px;
    line-height: 27px;
    font-weight: 700;
}
.tea-step__item.is-current ~ .tea-step__item .tea-step__item {
    color: #bbb;
}
.tea-step__item.is-current ~ .tea-step__item .tea-step__num {
    background-image: url(assets/image/slice/svg/step-back.svg);
    background-color: #fff;
    vertical-align: middle;
    color: #bbb;
}
.tea-step__item.is-current .tea-step__num:after,
.tea-step__item.is-current ~ .tea-step__item .tea-step__num:after{
    display: none;
}


.tea-step.tea-step--alternative .tea-step__item.is-current ~ .tea-step__item .tea-step__num{
    background-image: url(assets/image/slice/svg/step-back-alternative.svg);
    margin-right: 0;
}


.tea-step.tea-step--dot .tea-step__item.is-current .tea-step__num:before,
.tea-step.tea-step--dot .tea-step__item.is-current ~ .tea-step__item .tea-step__num:before {
    font-size: 0;
}
.tea-step.tea-step--dot .tea-step__item.is-current ~ .tea-step__item .tea-step__num{
    background-color:#bbb;
}
.tea-step.tea-step--dot .tea-step__item.is-current .tea-step__num{
    background-color: #fff;
    background-image: none;
    border: 2px solid #006eff;
}