/**
 * 1.表单验证
 */

.tea-form__controls {
    display: block;
    position: relative;
}

// 帮助文本：行内（默认）、块级
.tea-form__help-text {
    font-size: 12px;
    color: #888;
    vertical-align: middle;
    margin-top: 8px;
}

.tea-form__help-text--inline {
    display: inline-block;
    vertical-align: middle;
    font-size: 12px;
    margin-left: 5px;
}

.tea-form__controls .tea-icon-valid {
    display: none;
    margin-left: 10px;
}

