@charset "utf-8";
.tea-content {
  background-color: #f2f2f2;
  height: 100%;
  overflow: initial;
  padding-left: 0;
  padding-right: 0;
  
  &__header {
    margin: 0;
    font-size: 0;
    height: auto;
    line-height: inherit;
    min-height: inherit;
    position: relative;
    border-bottom: 1px solid #ddd;
    margin-bottom: 20px;
    padding: 10px 20px;
    color: #000;
    background: #fff;
    & h2 {
      display: inline-block;
      vertical-align: middle;
      font-size: 16px;
      font-weight: 700;
      margin-right: 20px;
      max-width: 70%;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      height: 30px;
      line-height: 30px;
    }
  }

  &__body {
    background-color: #f2f2f2;
    padding: 0 20px 20px;
  }

  &__inner {
    max-width: 1360px;
    margin: 0 auto;
  }
}


.tea-content__inner.tea-content_inner--full {
  max-width: 100%;
  margin: 0 auto;
}

.tea-content__header-back {
  height: 26px;
  line-height: 26px;
  width: 26px;
  box-sizing: border-box;
  display: inline-block;
  text-align: center;
  margin-left: -6px;
  vertical-align: middle;
  margin-right: 24px;
}

.tea-content__header-back:hover {
  background-color: rgba(0,0,0,.05);
}

.tea-content__header-back .tea-icon-btnback{
  margin-top: 4px;
}