.tea-backdrop{
    background-color: #000;
    opacity: .5;
	bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
	overflow-y: auto;
	z-index: 1000;
}

.tea-dialog {
    text-align: center;
    position: fixed;
    left:0;
    top:0;
    right:0;
    bottom:0;
    z-index: 1001;
   overflow: auto;
   white-space: nowrap;
}
.tea-dialog::before{
    content: "";
    display: inline-block;
    width:0px;
    height:100%;
    vertical-align: middle;
}
.tea-dialog__inner.size-s { 
    width:420px;
}
.tea-dialog__inner.size-l {
    width: 800px;
}
.tea-dialog__inner.size-xl {
    width: 950px;
}
.tea-dialog__inner.size-auto {
    width: auto;
}
.tea-dialog__inner{
    text-align: left;
    white-space: normal;
    display: inline-block;
    vertical-align: middle;
    min-width: 420px;
    width: 550px;
    background-color: #fff;
    padding: 25px;
    box-sizing: border-box;
    box-shadow: 1px 1.732px 20px 0px rgba(0, 0, 0, 0.2);
}
.tea-dialog__header:after,
.tea-dialog__body:after,
.tea-dialog__footer:after {
    content: "";
    display: table;
    clear: both
}

// 标头
.tea-dialog__header {
    font-size: 14px;
    color: #000;
    position: relative;
    margin-bottom: 10px;
    min-height: 26px;
}
.tea-dialog__headertitle{
    font-weight: bold;
    margin-bottom: 14px;
    display: inline-block;
    font-size: 14px;
    line-height: 26px;
}
    // 关闭按钮
.tea-dialog__header .tea-btn--icon {
	position: absolute;
	top: 0;
	right: -7px;
}

