.tea-dialog__footer {
    border-top: 0;
    position: relative;
    margin-top: 30px;
}    
.tea-dialog__btnwrap {
    text-align: center;
    position: relative;
    z-index: 0;
    font-size: 0;
}
.tea-dialog__btnwrap > .tea-btn {
    margin: 0 5px;
}



