.tea-alert {
  font-size: 12px;
  line-height: inherit;
  padding: 10px 30px 10px 20px;
  vertical-align: middle;
  color: #003b80;
  border-color: #97c7ff;
  border-width: 1px;
  border-style: solid;
  border-radius: 2px;
  background: #e5f0ff;
  position: relative;
  box-sizing: border-box;
  max-width: 1360px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom:20px;

  &__info {
    font-size: 12px;
    line-height: inherit;
    > .tea-list {
      padding: 10px 0 0;
      li:last-of-type {
        margin-bottom: 0;
      }
    }
    .tea-list--bullet > li::before {
      background-color: #003b80;
    }
  }
  .tea-icon-close {
    position: absolute;
    top: 10px;
    right: 10px;
    &:hover {
      background-color:transparent
    }
  }

  &--warning {
    color: #c07400;
    border-color: #ffd18b;
    background-color: #fff4e3;
  }
  &--error {
      color: #b43537;
      border-color: #f6b5b5;
      background-color: #fcecec;
  }
  &--success {
    color: #007e3b;
    border-color: #9ce4bc;
    background-color: #e6f8ee;
  }

}


