.tea-tree{
    position: relative;
    max-width:420px;    //经设计确认，默认宽度为420，若实际场景有差异，可再调整
}
.tea-tree__node{
    position: relative;
    line-height: 1;
    background-color:#fff;
    white-space: nowrap;
    
    &.is-selected{
        >.tea-tree__node-content{
            background-color: #f2f2f2;
        }
            
    }
}
.tea-tree__node-content{
    font-size: 0;
    padding: 7px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;
    &:hover{background-color: #f2f2f2;}
}
.tea-tree__switcher{
    display: inline-block;
    margin-right:5px;
    width:16px;
    height: 16px;
}
.tea-tree__label{
    display: inline-block;
    font-size: 12px;
    vertical-align: middle;
    overflow: hidden;
    text-overflow: ellipsis;
    .tea-icon{
        margin-right:2px;
    }
}
.tea-tree__label-title{
    display: inline-block;
    vertical-align: middle;
    line-height:1.5;
}
.tea-tree .tea-form-check{
    padding-left:26px;
}
.tea-tree .tea-checkbox{
    margin-right:10px;
}

.tea-tree__action{
    position: absolute;
    right:0;
    top:0;
    background-color:rgba(242,242,242,0.95);
    display: none;
    padding-left:10px;

    .tea-text--link{
        font-size: 12px;
        vertical-align: middle;
        margin-right:10px;
        line-height: 30px;
        display: inline-block;
        &:hover{
            text-decoration: none;
        }
    }
}
.tea-tree__node-content{
    &:hover{
        .tea-tree__action{
            display: block;
        }
    }
}

