@charset "utf-8";
.tea-table {
    background-color: #fff;
    overflow: inherit;
}

/* 公共类 */
.tea-table a {
    display: inline-block;
    vertical-align: middle;
    color: #006eff;
    text-decoration: none;
}
.tea-table a:hover {
    text-decoration: underline;
} 
.tea-table .tea-form-check .tea-checkbox{top:1px;}

/* 表头 */
.tea-table thead {
	color: #666;
	font-size: 12px;
	font-weight: bold;
	/* line-height: 30px; */
	margin-right: 0;
	width: 100%;
	/* background-color: #f6f8fa; 
	border-bottom: 1px solid #d1d5de;
	border-top: 1px solid #d1d5de;*/
}
.tea-table thead tr{
    background-color: #fff;
}
.tea-table__header{
    background-color: #fff;
    border-bottom: 1px solid #ddd;
}
.tea-table__header thead{
    background-color: transparent;
    border:0 none;
}
.tea-table__header .tea-icon{
    position: absolute;
    top: 1px;
    margin-left:4px;
}
.tea-table__header .tea-dropdown{
    max-width: 100%;
}
.tea-table__header .tea-dropdown__header .tea-icon{
    top:6px;
}
.tea-table__headerop{
    max-width: 88%;
}
.tea-table__sortbtn {
	color: #888;
	cursor:pointer;
    display: block;
    position: relative;
    padding-right:10px;
}
.tea-table__sortbtn.is-active,
.tea-table__sortbtn:hover {
    color: #444;
}
.tea-table__sortbtn.is-active .tea-icon-sort,
.tea-table__sortbtn:hover .tea-icon-sort{
    background-image: url(assets/image/slice/svg/sort-arrow-icon.svg?fill=444)
}

