.tea-table__box {
    border-collapse: collapse;
    border-spacing: 0;
    table-layout: fixed;
    width: 100%;
    position: relative;
}

.tea-table__body {
    border-bottom: 1px solid #e5e5e5;
    // overflow: inherit;
    // overflow-y: auto;
}
.tea-table__box tr{
    outline:0 none;
}

.tea-table__box th,
.tea-table__box td {
    text-align: left;
    vertical-align: middle;
    font-size: 0;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: initial
}
.tea-table__box th {
    position: relative;
    padding:10px;
}
.tea-table__box td {
    border-bottom: 1px solid #e5e5e5;
    color: #444;
    padding-top: 14px;
    padding-bottom: 14px;
    height: auto;
}
.tea-table--compact .tea-table__box td{
    padding-top:6px;
    padding-bottom:6px;
}
.tea-table__box th > div,
.tea-table__box td > div
{
    display:inline-block;
    vertical-align: middle;
    font-size: 12px;
    padding-left: 20px;
    padding-right: 10px;
    width: 100%;
    // line-height: 18px;
    padding: 0;
    word-wrap: break-word;
}

.tea-table__box th > div{
    /* height: 40px; */
    position: relative;
    color: #888;
    /* line-height: 40px; */
    padding: 0;
}
.tea-table__box tr:last-child td{
    border-bottom: none;
}
.tea-table__box tr:last-child td td{
    border-bottom:1px solid #e5e5e5;
}
.tea-table__box tr:last-child td tr:last-child td{
    border-bottom: none;
}




.tea-table__box tbody tr .hover-icon{
    display: none;    
}
.tea-table__box tbody tr:hover .hover-icon{
    display: inline-block;
}
.tea-table__box tbody tr.new-row{
    background-color:#FFF8E4;
}
.tea-table__box tbody tr.new-row .new-mark-icon{
    margin-left:3px;
}
.tea-table__box tbody tr.is-selected,
.tea-table__box tbody tr:hover,
.tea-table__box tbody tr:focus {
    background-color: #f2f2f2;
}
.tea-table__box tbody tr.is-disabled {
    background-color:#fff;
    opacity: 0.5;
    cursor: not-allowed;
}


.tea-table__box tr.is-disabled td,
.tea-table__box tr.is-disabled td a,
.tea-table__box tr td a.is-disabled
{
    /* color: #bbb; */
    /* cursor: default; */
    cursor: not-allowed;
    text-decoration: none;
}

.tea-table__box tbody tr:last-child td{
    /* border-bottom: none; */
}


