/* 空白行 */
.tea-table__box .tea-table__tr--blank{height:98px;}
.tea-table__box .tea-table__tr--blank:hover{background-color: #fff;}
.tea-table__box .tea-table__tr--blank td{ text-align:center; vertical-align: middle;}
.tea-table__box .tea-table__tr--blank td .tea-icon{vertical-align: -3px;margin-right: 10px;}
.tea-table__box .tea-table__tr--blank td a{vertical-align:baseline;}


/*detail row*/
.tea-table__body .tr__detailrow>td{
    background-color:#fafafa;
}
