.tea-table__body .tr__detailrow .tea-table{
    border:1px solid #ddd;
    border-bottom: none;
}

.tea-table__body .tr__detailrow .tea-table__header tr:hover{
    background-color: #fff;
}
.tea-table__body .tc-group-row{
    background-color: #f7f7f7;
}
.tea-table__body .tc-group-row .text-label{
    margin-left: 20px;
}

/* 顶部对齐 */
.tea-table.tea-table--verticaltop{}
.tea-table.tea-table--verticaltop .tea-table__box td{
    vertical-align: top;
}

// 垂直滚动（手动给tea-table__body定高）
.tea-table.tea-table--scrollable .tea-table__body{overflow: auto;}

// loading、加载失败的占位提示
.tea-info-row {
  padding: 30px 0;
  text-align: center;
  vertical-align: middle;
}

.tea-table__action-panel{
    margin-bottom:10px;
}



