.tea-combobox{
    position: relative;
    width:200px;
    display: inline-block;
    
    .tea-input{
        padding-right:40px;
    }
}
.tea-combobox__btn{
    display: inline-block;
    top:1px;
    bottom:1px;
    right:1px;
    position: absolute;
    border-left: 1px solid #ddd;
    padding:7px;
    &:hover{
        background-color: #f2f2f2;
    }
    .tea-icon{
        vertical-align: top;
        margin-top:-1px;
    }
}