@charset "utf-8";
.tea-card{
    background-color: #fff;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.2);
    max-width: 1360px;
    margin-left: auto;
    margin-right: auto;
    box-sizing: border-box;
}
.tea-card + .tea-card{
    margin-top:20px;
}
.tea-card__header{
    border-bottom: 1px solid #ddd;
}
.tea-card__header h3,
.tea-card__header .tea-h3{
    padding:20px;
}
.tea-card__subtitle{
    font-size: 12px;
    color: #888;
    margin-left: 5px;
    font-weight: normal;
}
.tea-card__btn{
    display: inline-block;
    text-align: center;
    padding: 5px;
    vertical-align: initial;
    text-decoration: none;
    line-height: initial;
    font-size: 12px;
}
.tea-card__btn:hover {
    background-color: rgba(0, 0, 0, 0.05);
    text-decoration: none;
}
.tea-card__body{
    font-size: 12px;
    padding:20px;
}
.tea-card__footer{
    padding: 20px;
    border-top: 1px solid #ddd;
    font-size: 12px;
}
.tea-card__title{
    margin-bottom:18px;
}
.tea-card.tea-card--bordered{
    box-shadow: none;
    border: 1px solid #ddd;
}