// radio button
.tea-radio,
input[type=radio] {
    width: 16px;
    height: 16px;
    cursor: pointer;
    vertical-align: middle;
    outline: none;
    -webkit-appearance: none;
    border: 1px solid #bbb;
    border-radius: 100%;
    background-position: inherit;
    background-color: #fff;
}

.tea-radio:hover,
.tea-radio:focus,
input[type=radio]:hover,
input[type=radio]:focus {
    border-color: #888;
    background-position: inherit;
}

.tea-radio:checked,
input[type=radio]:checked {
    background-image: url(assets/image/slice/svg/radio.svg);
    background-color: #006eff;
    border: 1px solid transparent;
    background-position: inherit;
}

.tea-radio:checked:hover,
input[type=radio]:checked:hover,
.tea-radio:checked:focus,
input[type=radio]:checked:focus {
    background-color: #0063e6;
}

.tea-radio[disabled],
input[type=radio][disabled] {
    background-color: #f2f2f2;
    background-position: inherit;
    cursor: default;
}

.tea-radio[disabled]:hover,
.tea-radio[disabled]:focus,
input[type=radio][disabled]:hover,
input[type=radio][disabled]:focus {
    border-color: rgba(187, 187, 187, 0.99);
}

.tea-radio[disabled]:checked,
input[type=radio][disabled]:checked {
    background-image: url(assets/image/slice/svg/radio.svg);
    background-color: #ddd;
    border: 1px solid rgba(187, 187, 187, 0.99);
    background-position: inherit;
}

//checkbox
.tea-checkbox,
input[type=checkbox] {
    width: 16px;
    height: 16px;
    cursor: pointer;
    vertical-align: middle;
    outline: none;
    -webkit-appearance: none;
    border: 1px solid #bbb;
    background-color: #fff;
}

.tea-checkbox:hover,
.tea-checkbox:focus,
input[type=checkbox]:hover,
input[type=checkbox]:focus {
    border-color: #888;
}

.tea-checkbox:checked,
input[type=checkbox]:checked {
    background-image: url(assets/image/slice/svg/checkbox.svg);
    background-color: #006eff;
    border-color: #006eff;
    background-position: inherit;
}

.tea-checkbox:checked:hover,
.tea-checkbox:checked:focus,
input[type=checkbox]:checked:hover,
input[type=checkbox]:checked:focus {
    background-image: url(assets/image/slice/svg/checkbox.svg);
    background-color: #0068f2;
    background-position: inherit;
}

.tea-checkbox[disabled],
input[type=checkbox][disabled] {
    background-image: none;
    background-color: #ddd;
    border-color: #bbb;
    background-position: inherit;
    cursor: default;
}

.tea-checkbox[disabled]:checked,
input[type=checkbox][disabled]:checked {
    background-image: url(assets/image/slice/svg/checkbox.svg);
    background-position: inherit;
}

.tea-checkbox[disabled]:checked:hover,
input[type=checkbox][disabled]:checked:hover {
    background-color: #ddd;
    border: 1px solid #bbb;
    cursor: default;
}

.tea-checkbox:indeterminate,
input[type=checkbox]:indeterminate {
    background-image: url(assets/image/slice/svg/indeterminate.svg);
    background-color: #006eff;
    border: 1px solid #006eff;
    background-position: inherit;
}

.tea-checkbox:indeterminate:hover,
input[type=checkbox]:indeterminate:hover {
    background-color: #0068f2;
}

.tea-checkbox[disabled]:indeterminate,
input[type=checkbox][disabled]:indeterminate {
    background-image: url(assets/image/slice/svg/indeterminate.svg);
    background-color: #ddd;
    background-position: inherit;
    border: 1px solid #bbb;
}

//容器
.tea-form-check {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding-left: 21px; //icon width: 16 + spacing 5 
    margin-top: 0;
    margin-right: 18px;
}
.tea-form-check:last-child{
    margin-right: 0px;
}
.tea-form-check__label {
    line-height: 16px;
    cursor: pointer;
    font-size: 12px;
    vertical-align: middle;
}

.tea-form-check .tea-radio,
.tea-form-check .tea-checkbox {
    position: absolute;
    top: 0;
    left: 0;
}

.tea-checkbox[disabled]+.tea-form-check__label,
.tea-radio[disabled]+.tea-form-check__label {
    cursor: default;
    cursor: not-allowed;
}

.tea-form-check--block {
    display: block;
}

.tea-form-check--block+.tea-form-check--block {
    margin-top: 8px;
    margin-right: 0;
}

// 主要用于表格中，行选择的时候，会把checkbox和文本放到一个td下，确保内部的overflow会生效
// 前端组件也只会在这个情况下添加此类名
.tea-form-check--table-select{
    max-width: 100%;
    box-sizing: border-box;
}