.tea-transfer {

  &__header {
    & h4 {
      margin-right: 10px;
    }
  }

  &__body {
    width: 100%;
    display: table;
    table-layout: fixed;
  }

  &__title {
    & h4 {
      font-size: 12px;
      line-height: 30px;
    }
  }

  &__cell {
    display: table-cell;
    position: relative;
  }

  &__content {
    

  }
  &__inner {
    height: 350px;
    border: 1px solid #ddd;
    overflow-x: hidden;
    overflow-y: auto;
  }
  &__separator {
    display: table-cell;
    width: 38px;
    text-align: center;
    vertical-align: middle;
  }

  &__footer {

  }
  
  &__tips {
    margin-top: 5px;
  }

  & .tea-search {
    margin-bottom: -1px;
  }
}

.tea-transfer__cell:last-of-type .tea-transfer__inner {
  height: 380px;
}
