.tea-justify-grid {
    display: flex;
    width: 100%;
    align-items:center;
    
    & .tea-justify-grid__col {
        flex:auto;
        vertical-align: middle;
        text-align: right;
        font-size: 0;

        .tea-search{
            display: inline-block;
            width:180px;
            vertical-align: middle;
        }
    }

    & .tea-justify-grid__col--left,
    & .tea-justify-grid__col:first-child {
        text-align: left;

        .tea-btn,
        .tea-select,
        .tea-dropdown,
        .tea-combobox,
        .tea-segment{
            margin-right: 10px;
        }
        
    }
    & .tea-justify-grid__col--right,
    & .tea-justify-grid__col--right:first-child{
        text-align: right;

        .tea-btn,
        .tea-select,
        .tea-dropdown,
        .tea-combobox,
        .tea-segment{
            margin-left: 10px;
            margin-right:0;
        }
        .tea-btn--icon{
            margin-left:0;
        }
    }
}

