.tea-search {
  position: relative;
  height: 30px;
  box-sizing: border-box;
  &__inner {
    position: relative;
    height: 100%;
  }
  &--multi {
    & .tea-input--search {
      &:focus {
        height: 150px;
        z-index: 3;
        overflow-y: auto;
      }
    }
  }
  &--simple {
    & .tea-input--search {
      border-top: transparent;
      border-right: transparent;
      border-left: transparent;
      padding: 0;
    }
  }
  .tea-btn {
    position: absolute;
    z-index: 4;
    padding: 6px 7px;
    bottom: 0px;
    &--search {
      right: 0px;
    }
    &--dismiss {
      right: 30px;
    }
    &--info {
      right: 30px;
    }
  }

  &__tags {
    display: inline-block;
  }
  &__tips {
    display: inline-block;
    line-height: 20px;
    font-size: 12px;
    margin-top: 4px;
  }

}



