@charset "utf-8";
.tea-dropdown {
    display: inline-block;
    vertical-align: middle;
    position: relative;
}

.tea-dropdown.tea-dropdown--width-m .tea-dropdown__header,
.tea-dropdown-box.tea-dropdown--width-m {
    width: 200px;
}

.tea-dropdown__header {
    height: 30px;
    padding: 0 22px 0 0;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;
}

.tea-dropdown-btn.tea-dropdown__header {
    border: 1px solid #DDDDDD;
    padding-left: 10px;
    background-color: #fff;
}

.tea-dropdown-btn.tea-dropdown__header:hover,
.is-expanded.tea-dropdown-btn .tea-dropdown__header {
    background-color: #F2F2F2;
}

.tea-dropdown__header .tea-icon {
    position: absolute;
    right: 4px;
    top: 6px;
}

.tea-dropdown__value {
    line-height: 30px;
    font-size: 12px;
    white-space: nowrap;
    overflow: hidden;
}

.tea-dropdown-btn .tea-dropdown__value {
    line-height: 28px;
}

.tea-dropdown-box {
    border: 1px solid #ddd;
    background-color: #fff;
    box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .2);
    min-width: 150px;
    min-height: 100px;
    // display: none;
    position: absolute;
}


/*.is-expanded .tea-dropdown__body {
    display: block;
    cursor: default;
    z-index: 1;
}*/

.tea-dropdown-box .tea-option-list {
    max-height: 180px;
    overflow-y: auto;
}

.tea-option-list__item {
    font-size: 12px;
    padding: 6px 10px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    color: #000;
    display: block;
    cursor: pointer;
}

.tea-option-list__label {
    font-weight: 700;
    cursor: default;
    margin: 0;
    padding: 0 10px;
    height: 30px;
    line-height: 30px;
}

.tea-option-list__item:hover {
    background-color: #F2F2F2;
}

.tea-option-list__label:hover {
    background-color: #FFF;
}

// 放带label标签的checkbox，把响应区域填充满
.tea-option-list__item.tea-option-list__item--label {
    padding: 0;
}

.tea-option-list__item--label .tea-form-check {
    padding: 6px 10px 6px 31px;
}

.tea-option-list__item--label .tea-checkbox {
    top: 6px;
    left: 10px;
}

