.tea-region {
  display: inline-block;
  vertical-align: middle;
  cursor: pointer;
  padding: 20px;
  width: 100%;
  min-width: 100px;
  box-sizing: border-box;

  &__title {
    font-size: 0;
    color: #444;
    margin-bottom: 16px;
    padding: 0;
    min-width: 140px;
    display: inline-block;
    line-height: 30px;
    cursor: pointer;
  }
  &__globe {
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 20px;
    background-image: url("assets/image/slice/svg/globe-icon.svg");
  }
  &__all {
    vertical-align: middle;
    padding-left: 5px;
    font-size: 12px;
  }

  &__list {
    display: table;
  }
  &__col {
    display: table-cell;
    min-width: 140px;
    padding-right: 2px;
  }
  &__unit {
    margin-bottom: 38px;
  }
  &__type {
    font-size: 12px;
    color: #888;
    line-height: 18px;
    padding: 0 6px;
    margin-bottom: 10px;
  }
  &__item {
    font-size: 0;
    color: #000;
    line-height: 30px;
    padding: 0 5px;
    cursor: pointer;
    margin-bottom: 2px;
    &:hover{
      background-color: #f2f2f2;
    }
  }
  &__flag {
    width: 20px;
    height: 20px;
    display: inline-block;
    vertical-align: middle;
    border-radius: 20px;
  }
  &__name {
    vertical-align: middle;
    font-size: 12px;
    margin-left: 5px;
  }
  &__new{
    width: 6px;
    height: 6px;
    display: inline-block;
    background-color: #e54545;
    border-radius: 12px;
    margin-left: 5px;
    vertical-align: text-top;
  }

}

.tea-region__item.is-selected {
  background-color: #f2f2f2;
}
.tea-region__col .tea-region__unit:last-child, .tea-region__unit .tea-region__item:last-child {
    margin-bottom: 0;
}

// 地域选择
.tea-dropdown.tea-dropdown--region-select {
  .tea-dropdown__header {
    background-color: #fff;
  }
  .tea-dropdown__header.tea-dropdown-btn {
    border: 1px solid #DDD;
    height: 30px;
    line-height: 28px;
    padding: 0 22px 0 10px;
    position: relative;
    box-sizing: border-box;
    font-size: 0;
  }
  .tea-region {
    &__item {
      display: inline-block;
      line-height: 28px;
      margin-bottom: 0;
      vertical-align: top;
      padding: 0;
    }
    &__more {
      font-size: 12px;
      color: #888;
      line-height: 28px;
      padding-left: 20px;
      display: table-cell;
      vertical-align: top;
      display: inline-block;
    }
  }
  .tea-dropdown-btn .tea-dropdown__value {
    line-height: 28px;
    display: inline-block;
  }
}

.tea-dropdown.tea-dropdown--region-select.is-active .tea-dropdown__header {
  background-color: #f2f2f2;
}

.tea-regionempty {
  display: table;
  margin: 50px auto;
  &__icon {
    display: table-cell;
  }
  &__content {
    display: table-cell;
    padding-left: 10px;
  }
  &__title {
    font-size: 16px;
    color: #000;
    line-height: 24px;
  }
  &__info {
    font-size: 12px;
    color: #888;
    line-height: 18px;
    margin-top: 10px;
  }
  &__inner {
    background-color: #fff;
    box-shadow: 0 1px 4px 0 rgba(0,0,0,.2);
    min-width: 155px;
    min-height: 100px;
    display: table;
    margin-top: 20px;
  }
  &__tips {
    font-size: 12px;
    color: #888;
    line-height: 18px;
    margin-top: 20px;
  }
}
