/*
 *
 * 日期选择器
 *
 */

.tea-calendar {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width:300px;
}

.tea-calendar__header,
.tea-calendar__footer {
    padding: 0 15px;
}

.tea-calendar__header{
    border-bottom:1px solid #ddd;
    padding-top:10px;
    padding-bottom:10px;
    margin-bottom:10px;
}


.tea-calendar__body {
    @include clearfix();
    display: flex;
}


/*
 * 月份
 */

.tea-calendar__table {
    flex:1;
    border-collapse: collapse;
    border-spacing: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    
    position: relative;
}

.tea-calendar__table+.tea-calendar__table {
    margin-left: 30px;
}

.tea-calendar__table .tea-pagination{
    position: absolute;
    top:8px;
    right:0;
}
.tea-calendar__table .tea-pagination__turnbtn {
    position: absolute;
}

.tea-calendar__table .tea-pagination__prebtn {
    right: 55px;
}
.tea-calendar__table .tea-pagination__curbtn {
    right: 31px;
}

.tea-calendar__table .tea-pagination__nextbtn {
    right: 7px;
}

.tea-calendar__caption {
    flex:100% 0 0;
    padding:0 15px;
    text-align: left;
    font-weight: bold;
    color: #000;
    line-height: 40px;
    font-size: 12px;
    >a{
        color:#444;
    }
    >a:hover{
        color:#006eff;
        text-decoration: none;
    }
}

.tea-calendar__header-group {
    // display: table-header-group;
    display: flex;
    flex: 1;
}

.tea-calendar__row {
    display: flex;
    flex:1;
    // display: table-row;
}

.tea-calendar__row-group {
    font-size: 0;
    flex: 100%;
    // display: table-row-group;
}

.tea-calendar__type-wrap{
    display: flex;
    flex-wrap: wrap;
    padding:0 10px 10px;
}

.tea-calendar__cell {
    flex:auto;
    text-align: center;
    vertical-align: middle;
    padding: 0px 4px;
    white-space: nowrap;
    position: relative;
    font-size: 0;
    cursor: pointer;
}

.tea-calendar__header-group .tea-calendar__cell {
    cursor: default;
}

.tea-calendar__cell>span {
    display: inline-block;
    width: 30px;
    height: 30px;
    line-height: 32px;
    border-radius: 100%;
    font-size: 13px;
    position: relative;
    z-index: 1;
}

.tea-calendar__cell:hover>span {
    background-color: #f2f2f2;
}

.tea-calendar__header-group .tea-calendar__cell:hover span {
    background-color: transparent;
}

// 今日
.tea-calendar__cell--now {
    color: #006eff;
    font-weight: bold;
}

// 超出本月日期
.tea-calendar__day--outside {
    color: #bbb;
    cursor: default;
}

.tea-calendar__day--outside:hover>span {
    background-color: transparent;
}

// 选中日期
.tea-calendar__cell.is-selected>span,
.tea-calendar__cell.is-selected:hover>span {
    background-color: #006eff;
    color: #fff;
}


/* 时间列表 */

.tea-list.tea-list--calendar-time {
    flex:1 0 88px;
    border-left: 1px solid #ddd;
    border-top:1px solid #ddd;
    max-height: 190px;
}

.tea-list.tea-list--calendar-time:first-child {
    border-left-color: transparent;
}


/* 放入下拉面板内 */

 


/* 日期选择器 */

.tea-datepicker,
.tea-monthpicker,
.tea-datetimepicker {
    display: inline-block;
    vertical-align: middle;
    // position: relative;  奇怪的bug
    & .tea-input {
        cursor: pointer;
    }
}

.tea-datepicker__input,
.tea-monthpicker__input,
.tea-datetimepicker__input {
    position: relative;
    display: inline-block;
    vertical-align: middle;
}

.tea-datepicker__input::after,
.tea-monthpicker__input::after,
.tea-datetimepicker__input::after {
    content: "";
    position: absolute;
    top: 7px;
    right: 10px;
    width: 16px;
    height: 16px;
    background-image: url(assets/image/slice/svg/calendar.svg);
    cursor: pointer;
}


/* 月份选择器 */

.tea-monthpicker {}


/* 时间选择器 */
.tea-timepicker {
     
}
.tea-timepicker__input {
    display: inline-block;
    vertical-align: middle;
    position: relative;
    & .tea-input {
        width: 105px;
        padding-right: 36px;
    }

    &.size-l{
        .tea-input{
            width:180px;
        }
    }
    &::after {
        content: "";
        position: absolute;
        top: 7px;
        right: 10px;
        width: 16px;
        height: 16px;
        background-image: url(assets/image/slice/svg/clock.svg);
        cursor: pointer;
    }
}


/* 日期时间选择 */
.tea-datetimepicker__input {
    & .tea-input {
        width: 287px;
    }
}

 


/* 预设快捷日期条 */

.tea-datepicker-bar {
    display: inline-block;
    vertical-align: middle;
    font-size: 0;
    & .tea-segment .tea-btn {
        padding-left: 15px;
        padding-right: 15px;
    }
    // & .tea-segment .tea-btn__pickdate {
    //     min-width: 100px;
    //     position: relative;
    //     text-align: left;
    //     padding-left: 10px;
    //     padding-right: 36px;
    // }
    // & .tea-segment .tea-btn__pickdate::after {
    //     content: "";
    //     position: absolute;
    //     top: 6px;
    //     right: 10px;
    //     width: 16px;
    //     height: 16px;
    //     background-image: url(assets/image/slice/svg/calendar.svg);
    // }
    & .tea-datepicker,
    & .tea-datetimepicker {
        margin-left: 10px;
    }
}

.tea-calendar__footer {
    border-top: 1px solid #ddd;
    padding: 15px;
    & .tea-justify-grid {
        font-size: 0;
    }
    & .tea-justify-grid__col--right .tea-btn+.tea-btn {
        margin-left: 10px;
    }
    & .tea-date-status {
        font-size: 12px;
        color: #888;
    }
    & .tea-date-status span {
        color: initial;
        display: inline-block;
        margin: 0 5px;
    }
    
}


// 双日历
.tea-calendar--date-range {
    width: 600px;
    & .tea-calendar__cell.is-selected:not(.tea-calendar__day--start):not(.tea-calendar__day--end)::before {
        content: "";
        position: absolute;
        top: 2px;
        left: 0;
        right: 0;
        bottom: 2px;
        background-color: #f2f2f2;
    }
    & .tea-calendar__cell.is-selected:not(.tea-calendar__day--start):not(.tea-calendar__day--end)>span {
        background-color: transparent;
        border-radius: 0;
        color: inherit;
    }
    & .tea-calendar__day--start::after,
    & .tea-calendar__day--end::after {
        content: "";
        position: absolute;
        right: 0;
        top: 2px;
        bottom:2px;
        width: 15px;
        background-color: #f2f2f2;
    }
    & .tea-calendar__day--end::after {
        left: 0;
        right: auto;
    }
}

// 选择月
.tea-calendar--month{
    .tea-calendar__cell>span {
        
        width: 36px;
        height: 36px;
        line-height: 38px;
    }
    .tea-calendar__cell{
        padding: 12px 15px;
    }
}

// 选择年
.tea-calendar--year{

    .tea-calendar__cell>span {
        width: 36px;
        height: 36px;
        line-height: 38px;
    }
    .tea-calendar__cell{
        padding: 0 15px;
    }

}


// 选择时间
.tea-calendar--time,
.tea-dropdown-box .tea-calendar--time{
    // .tea-calendar__wrap{
    //     padding-top:0;
    // }
    .tea-calendar__type-wrap{
        padding: 0;
    }
    .tea-calendar__caption{
        text-align: center;
    }
    .tea-pagination{
        display: none;
    }
    .tea-calendar__row-group{
        // margin-top:-1px;
    }
    .tea-calendar__body{
        padding:0;
    }
}