
.tea-tag-group {
  display: inline-block;
  font-size: 0;
  line-height: normal;
  vertical-align: top;
}
.tea-tag {
  display: inline-block;
  position: relative;
  background-color: #f2f2f2;
  color: #000;
  margin: 4px 4px 0px 0;
  padding: 0 5px 0 5px;
  height: auto;
  line-height: 20px;
  font-size: 0;
  vertical-align: middle;
  border:1px solid #ddd;
  span {
    display: inline-block;
    font-size: 12px;
    vertical-align: top;
    white-space: nowrap;
    max-width:100px;
    overflow: hidden;
    text-overflow: ellipsis;
    word-wrap: break-word;
  }
  .tea-icon-dismiss {
    display: none;
    position: absolute;
    right:2px;
    bottom:2px;
    &:hover {
      cursor: pointer;
    }
  }

  &--edit {
    padding: 0 20px 0 5px;
    span{
      display: inline;
      white-space:normal;
    }
    .tea-icon-dismiss{
      display: inline-block;
    }
    
  }
}
